import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import { Button, GridList, LoadMore, AppBar, Icon } from 'muse-ui';
import 'muse-ui/lib/styles/theme.less';
import 'muse-ui/lib/styles/base.less';
// @ts-ignore
import theme from 'muse-ui/lib/theme';
import fly from 'flyio';




theme.add('pink', {
  primary: '#ea80d1',
  secondary: '#ff4081',
  success: '#4caf50',
  warning: '#ffeb3b',
}, 'light');
theme.use('pink');

Vue.prototype.$fly = fly;
Vue.use(Icon);
Vue.use(AppBar);
Vue.use(LoadMore);
// Vue.use(Paper);
Vue.use(GridList);
Vue.use(Button);
// Vue.use(BottomNav);
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
