import Vue from 'vue'
import VueRouter from 'vue-router'
import IndexPage from '@/views/listPage.vue';
import UploadImg from "@/views/UploadImg.vue";


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: IndexPage,
  },
  {
    path: '/upload',
    name: 'uploadImg',
    component: UploadImg,
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router;
